import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'
import { $http } from '@escook/request-miniprogram'
// 1. 导入 store 的实例对象
import store from './store/store.js'

uni.$http = $http

//请求拦截器
$http.beforeRequest  = function(options){
	uni.showLoading({
		title:"数据加载中"
	})
}

//响应拦截器
$http.afterRequest = function(){
	uni.hideLoading()
}

//请求根路径
$http.baseUrl = 'https://api-hmugo-web.itheima.net'


//封装弹窗方法
uni.$showMsg = function(title="系统提示",duration = 1500){
	uni.showToast({
		title,
		duration,
		icon:"none"
	})
}

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App,
	store
})
app.$mount()